import React, { Fragment } from 'react';
import { graphql } from 'gatsby';
import PropTypes from 'prop-types';

import Hero from '../components/Hero';
import Social from '../components/Social';
import SEO from '../components/Seo';
import { useSiteData } from '../hooks';

const IndexPage = ({ location, data }) => {
  const { avatar, contentfulPage } = data;
  const { metadata: pageMetadata, hero } = contentfulPage;
  const { metadata: siteMetadata } = useSiteData();

  return (
    <Fragment>
      <SEO title={pageMetadata.title} keywords={[pageMetadata.keywords]} />
      <Hero avatar={avatar} content={hero} />
      <Social links={siteMetadata.socialLinks} />
    </Fragment>
  );
};

IndexPage.propTypes = {
  data: PropTypes.object.isRequired,
  location: PropTypes.object.isRequired,
};

export default IndexPage;

export const pageQuery = graphql`
  query {
    contentfulPage(name: { eq: "Home Page" }) {
      hero {
        titlePrefix
        title
        description
      }
      metadata {
        title
        keywords
      }
    }
    avatar: file(absolutePath: { regex: "/guy-profile-pic.jpg/" }) {
      childImageSharp {
        fixed(width: 150, height: 150) {
          ...GatsbyImageSharpFixed
        }
      }
    }
  }
`;
