import React from 'react';
import styled, { css } from 'styled-components';
import GatsbyImage from 'gatsby-image';
import PropTypes from 'prop-types';
import { github } from '../../constants';
import { animations } from '../../styles';

const Container = styled('div')`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;

const preStyles = ({ theme }) => css`
  margin: 0;
  align-self: end;
  color: ${theme.colors.p700};
  font-size: 24px;
  font-weight: ${theme.fontWeight.bold};
  animation: ${animations.fadeInUp} 0.6s 0.3s ease both;
`;

const PreTitle = styled('p')`
  ${preStyles}
`;

const titleStyles = ({ theme }) => css`
  color: ${theme.colors.p700};
  font-size: 75px;
  font-weight: ${theme.fontWeight.bold};
  margin: 0;
  animation: ${animations.fadeInUp} 0.6s 0.4s ease both;

  ${theme.mq.medium`
    margin: 0 10px 20px;
    font-size: 90px;
  `};
`;

const Title = styled('h1')`
  ${titleStyles}
`;

const subTitleStyles = ({ theme }) => css`
  color: ${theme.colors.p700};
  font-size: 23px;
  font-weight: ${theme.fontWeight.bold};
  animation: ${animations.fadeInUp} 0.6s 0.5s ease both;
`;

const SubTitle = styled('p')`
  ${subTitleStyles}
`;

const imageStyles = ({ theme }) => css`
  border-radius: 50%;
  left: 0;
  top: 0;
  border: 3px solid ${theme.colors.p600};
  margin: 0;
  animation: ${animations.fadeInDown} 0.6s 0.2s ease both;
`;

const Image = styled(GatsbyImage)`
  ${imageStyles}
`;

function Hero({ avatar, content }) {
  return (
    <Container>
      <Image fixed={avatar.childImageSharp.fixed} alt={github.MY_USERNAME} />
      <PreTitle>{content.titlePrefix}</PreTitle>
      <Title>{content.title}</Title>
      <SubTitle>{content.description}</SubTitle>
    </Container>
  );
}

Hero.propTypes = {
  avatar: PropTypes.object.isRequired,
  content: PropTypes.object.isRequired,
};

export default Hero;
